// axios  
import { ContentTypeEnum, RequestEnum, ResultEnum } from '@/enums/httpEnum';
import { isString, isUrl } from '@/utils/is';
import { formatRequestDate, joinTimestamp } from './helper';
import axios from 'axios';
import { VAxios } from './Axios';
import { deepMerge } from '@/utils';
import { useGlobSetting } from '@/hooks/setting';
import { checkStatus } from './checkStatus';
import { storage } from '@/utils/Storage';
import { ACCESS_TOKEN } from '@/store/mutation-types';
import router from '@/router';

const globSetting = useGlobSetting();
const urlPrefix = globSetting.urlPrefix || '';

/**
 * @description: 
 */
const transform = {
  /**
   * @description: 
   */
  transformRequestData: (res, options) => {
    const {
      isShowMessage = true,
      isShowErrorMessage,
      isShowSuccessMessage,
      successMessageText,
      errorMessageText,
      isTransformResponse,
      isReturnNativeResponse,
    } = options;
    //  
    if (isReturnNativeResponse) {
      return res;
    }

    // 
    // codedatamessage
    if (!isTransformResponse) {
      return res.data;
    }

    const { data } = res;

    const $dialog = window['$dialog'];
    const $message = window['$message'];

    if (!data) {
      // return '[HTTP] Request has no return value';
      throw new Error('');
    }
    //   codedatamessage 
    const { code, data: result, message } = data;
    // 
    const hasSuccess =
      data && Reflect.has(data, 'code') && code === ResultEnum.SUCCESS;
    // 
    if (isShowMessage) {
      if (hasSuccess && (successMessageText || isShowSuccessMessage)) {
        // 
        $dialog.success({
          type: 'success',
          content: successMessageText || message || '',
        });
      } else if (!hasSuccess && (errorMessageText || isShowErrorMessage)) {
        // 
        $message.error(message || errorMessageText || '');
      } else if (!hasSuccess && options.errorMessageMode === 'modal') {
        // errorMessageMode=custom-modalmodal
        $dialog.info({
          title: '',
          content: 'message',
          positiveText: '',
          onPositiveClick: () => {},
        });
      }
    }

    // 
    if (code === ResultEnum.SUCCESS) {
      return result;
    }
    //  
    let errMsg = message;
    switch (code) {
      // 
      case ResultEnum.ERROR:
        $message.error(errMsg);
        break;
      case ResultEnum.TOKEN_UNSUPPORTED:
      case ResultEnum.TOKEN_INVALID: {
        storage.clear();
        router.forward({ path: '/login' });
        break;
      }
    }
    throw new Error(errMsg);
  },

  // config
  beforeRequestHook: (config, options) => {
    const {
      apiUrl,
      joinPrefix,
      formatDate,
      joinTime = true,
      urlPrefix,
    } = options;

    const isUrlStr = isUrl(config.url);

    if (!isUrlStr && joinPrefix) {
      config.url = `${urlPrefix}${config.url}`;
    }

    if (!isUrlStr && apiUrl && isString(apiUrl)) {
      config.url = `${apiUrl}${config.url}`;
    }
    const params = config.params || {};
    const data = config.data || false;
    if (config.method?.toUpperCase() === RequestEnum.GET) {
      if (!isString(params)) {
        //  get 
        config.params = Object.assign(
          params || {},
          joinTimestamp(joinTime, false)
        );
      } else {
        // restful
        config.url = config.url + params + `${joinTimestamp(joinTime, true)}`;
        config.params = undefined;
      }
    } else {
      if (!isString(params)) {
        formatDate && formatRequestDate(params);
        if (
          Reflect.has(config, 'data') &&
          config.data &&
          Object.keys(config.data).length > 0
        ) {
          config.data = data;
          config.params = params;
        } else {
          config.data = params;
          config.params = undefined;
        }
      } else {
        // restful
        config.url = config.url + params;
        config.params = undefined;
      }
    }
    return config;
  },

  /**
   * @description: 
   */
  requestInterceptors: (config, options) => {
    // config
    const token = storage.get(ACCESS_TOKEN);
    if (token && config?.requestOptions?.withToken !== false) {
      // jwt token
      config.headers.Authorization = options.authenticationScheme
        ? `${options.authenticationScheme} ${token}`
        : token;
    }
    return config;
  },

  /**
   * @description: 
   */
  responseInterceptorsCatch: (error) => {
    const $dialog = window['$dialog'];
    const $message = window['$message'];
    const { response, code, message } = error || {};
    // TODO 
    const msg =
      response && response.data && response.data.message
        ? response.data.message
        : '';
    const err = error.toString();
    try {
      if (code === 'ECONNABORTED' && message.indexOf('timeout') !== -1) {
        $message.error('!');
        return;
      }
      console.log(err);
      if (err && err.includes('Network Error')) {
        $dialog.info({
          title: '',
          content: '',
          positiveText: '',
          //negativeText: '',
          closable: false,
          maskClosable: false,
          onPositiveClick: () => {},
          onNegativeClick: () => {},
        });
        return Promise.reject(error);
      }
    } catch (error) {
      throw new Error(error);
    }
    // 
    const isCancel = axios.isCancel(error);
    if (!isCancel) {
      checkStatus(error.response && error.response.status, msg);
    } else {
      console.log(error, '');
    }
    //return Promise.reject(error);
    return Promise.reject(response?.data);
  },
};

function createAxios(opt) {
  return new VAxios(
    deepMerge(
      {
        timeout: 10 * 1000,
        authenticationScheme: '',
        // 
        prefixUrl: urlPrefix,
        headers: { 'Content-Type': ContentTypeEnum.JSON },
        // 
        transform,
        // 
        requestOptions: {
          // prefix url
          joinPrefix: true,
          //  
          isReturnNativeResponse: false,
          // 
          isTransformResponse: true,
          // 
          formatDate: true,
          // 
          errorMessageMode: 'none',
          // 
          apiUrl: globSetting.apiUrl,
          // 
          urlPrefix: urlPrefix,
          //  
          joinTime: true,
          // 
          ignoreCancelToken: true,
          // token
          withToken: true,
        },
        withCredentials: false,
      },
      opt || {}
    )
  );
}

export const http = createAxios();
