export function checkStatus(status, msg) {
  const $message = window['$message'];
  switch (status) {
    case 400:
      $message.error(msg);
      break;
    // 401: 
    // 
    // 
    case 401:
      $message.error('!');
      break;
    case 403:
      $message.error('!');
      break;
    // 404
    case 404:
      $message.error('!');
      break;
    case 405:
      $message.error('!');
      break;
    case 408:
      $message.error('');
      break;
    case 500:
      $message.error(',!');
      break;
    case 501:
      $message.error('');
      break;
    case 502:
      $message.error('');
      break;
    case 503:
      $message.error('!');
      break;
    case 504:
      $message.error('');
      break;
    case 505:
      $message.error('http!');
      break;
    default:
      $message.error(msg);
  }
}
